body {
    background: #1f1f1f;
    font-family: monospace;
}

#app {
    max-width: 400px;
    margin: auto;
}

.calculator {
    display: grid;
    grid-template-columns: auto auto auto auto;
}

.span-2 {
    grid-column: span 2;
}

.display {
    grid-column: span 4;
    height: 37px;
    background: #2f2f2f;
    padding: 10px;
    font-size: 32px;
    border: 1px solid #1f1f1f;
    text-align: right;
    color: #f2f2f2;
}

.digit, .padding {
    background: #9f9f9f;
}

.digit:hover {
    background: #b9b9b9;
}

.digit:active {
    background: #808080;
}

.operator {
    color: #f2f2f2;
    background: #ff992b;
}

.operator:hover {
    background: #ffb25e;
}

.operator:active {
    background: #e38724;
}

.operator:hover {

}

button, div.padding {
    border: 1px solid #1f1f1f;
    padding: 10px;
    font-size: 24px;
    color: #1c1c1c;
}
